	module.exports = {"title":"Hello Gatsby","date":"2017-02-13T00:00:00.000Z","path":"/2017-02-13-hello-gatsby/","featuredImage":"https://i.imgur.com/R3RjKw2.png","body":"<p>每次都說要寫個小 blog 簡單紀錄自己學習的東西</p>\n<p>最後都是不了了之</p>\n<p>這次大概也是 QQ 吧</p>\n<!--more-->\n<h2>Static Site Generator</h2>\n<p>之前有用過 <a href=\"https://github.com/jekyll/jekyll\">Jekyll</a> 跟 <a href=\"https://github.com/hexojs/hexo\">Hexo</a></p>\n<p>這次用的是用最新潮 <s>Hype</s> 的 React 寫的 <a href=\"https://github.com/gatsbyjs/gatsby\">Gatsby</a></p>\n<p>我是在 React Conf 2016 的一個 Lightning Talk 看到他的</p>\n<p><div class=\"embed-responsive embed-responsive-16by9\"><iframe class=\"embed-responsive-item\" id=\"youtubeplayer\" type=\"text/html\" width=\"640\" height=\"390\" src=\"//www.youtube.com/embed/RFkNRKL6ZoE\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe></div></p>\n<p>最近要重新開始寫 Blog 的時候就想說來用看看</p>\n<p>用起來感覺超爽</p>\n<p>引用一下官網的 Why use Gatsby:</p>\n<ol>\n<li><strong>No-refresh page transitions</strong></li>\n</ol>\n<p>用 React 寫的 Blog 直接就是一個 <abbr title=\"Single Page Application\">SPA</abbr>，瀏覧文章換頁的時候不會像傳統 Web 一樣要重 Load 頁面，非常流暢</p>\n<ol start=\"2\">\n<li><strong>The awesome React.js component model</strong></li>\n</ol>\n<p>可以用 React Component 寫 Blog，真的潮 <s>Hype</s></p>\n<ol start=\"3\">\n<li><strong>Live editing on every part of your site. Dramatically speed development.</strong></li>\n</ol>\n<p>最重要的是，Webpack 的 hot reload，在寫文章跟改樣式的時候都可以存檔時自動刷新，超爽</p>\n<h2>Setup</h2>\n<ol>\n<li>安裝 Gatsby <code>npm install -g gatsby</code></li>\n<li>建立 Blog <code>gatsby new [name] [starter]</code></li>\n</ol>\n<p>Starter 就是別人寫好的 Gatsby Template，官方也有提供一些:</p>\n<ul>\n<li><a href=\"https://github.com/gatsbyjs/gatsby-starter-blog\">Simple blog</a></li>\n<li><a href=\"https://github.com/gatsbyjs/gatsby-starter-documentation\">Simple documentation site</a></li>\n</ul>\n<ol start=\"3\">\n<li>進到 blog 裡面跑 <code>gatsby develop</code> 他會在 <a href=\"localhost:8000\">localhost:8000</a> 跑開發版的 Blog，就可以開始爽用 hot reload 來寫東西</li>\n<li>要發佈的時候跑 <code>gatsby build</code> 他會把所有東西編好放在 <code>public/</code> 底下，然後可以用 <code>gatsby serve-build</code> 來測試</li>\n</ol>\n<h2>Structure</h2>\n<ul>\n<li><code>config.toml</code>: Blog 的設定檔，可以用 <code>import { config } from 'config'</code> 拿到</li>\n<li><code>pages/</code>: 這個資料夾底下的東西都會變成 Blog 的頁面，除了檔名底線開頭的以外，而 <code>_template</code> 會變成同一層目錄頁面的模板</li>\n<li>(optional) <code>gatsby-browser.js</code> 可以監聽一些事件:\n<ul>\n<li>Export <code>onRouteUpdate</code> function 可以監聽 React Router，像是拿來觸發 Google Analytics</li>\n</ul>\n</li>\n</ul>\n<h2>結語</h2>\n<p>基本上就是這樣</p>\n<p>希望用了一個這麼爽的 static site generator</p>\n<p>我就可以養成寫 Blog 的習慣 QQ</p>\n"}

