	module.exports = {"title":"Fluid Width Video","date":"2017-02-14T00:00:00.000Z","path":"/2017-02-14-fluid-width-video/","featuredImage":"https://i.imgur.com/n2IZyte.png","body":"<p>昨天發完文用手機看發現影片長寬沒有 Responsive</p>\n<p>今天趕緊來修一下</p>\n<p>發現有一些小事情要注意</p>\n<!--more-->\n<p>通常在網頁裡放影片有兩種方法</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">video</span> <span class=\"hljs-attr\">width</span>=<span class=\"hljs-string\">\"400\"</span> <span class=\"hljs-attr\">height</span>=<span class=\"hljs-string\">\"300\"</span> <span class=\"hljs-attr\">...</span> &gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">video</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">iframe</span> <span class=\"hljs-attr\">width</span>=<span class=\"hljs-string\">\"400\"</span> <span class=\"hljs-attr\">height</span>=<span class=\"hljs-string\">\"300\"</span> <span class=\"hljs-attr\">...</span> &gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">iframe</span>&gt;</span>\n</code></pre>\n<p>但是這樣直接把長寬寫死就不 Responsive</p>\n<p>很容易跟上面那張圖一樣影片凸出去</p>\n<p>這兩種嵌影片的方式分別有不同的做法</p>\n<h2>&lt;video&gt;</h2>\n<p>用 <code>&lt;video&gt;</code> 的話非常簡單</p>\n<p>可以直接用百分比設定</p>\n<p>高度就會根據 video source 等比例自動縮放</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">video</span> {\n  <span class=\"hljs-attribute\">width</span>: <span class=\"hljs-number\">100%</span>;\n  <span class=\"hljs-attribute\">height</span>: auto;\n}\n</code></pre>\n<h2>&lt;iframe&gt;</h2>\n<p>嵌入 Youtube 之類的影片通常是用 <code>&lt;iframe&gt;</code></p>\n<p><code>&lt;iframe&gt;</code> 不會隨著內容物縮放長寬</p>\n<p>所以如果用跟剛剛一樣的方式設定就會變成這樣</p>\n<p><img src=\"https://i.imgur.com/5ovTTT9.png\" alt=\"iframe\"></p>\n<p>解決辦法就是在 iframe 外面包一層 div</p>\n<p>然後把 div 設定成有 Responsive 的 16:9</p>\n<p>再讓 iframe 隨著他縮放</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"video-wrapper\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">iframe</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"...\"</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">iframe</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n</code></pre>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-class\">.video-wrapper</span> {\n  <span class=\"hljs-attribute\">position</span>: relative;\n  <span class=\"hljs-attribute\">padding-bottom</span>: <span class=\"hljs-number\">56.25%</span>; <span class=\"hljs-comment\">/* 16:9 */</span>\n}\n\n<span class=\"hljs-selector-class\">.video-wrapper</span> <span class=\"hljs-selector-tag\">iframe</span> {\n  <span class=\"hljs-attribute\">position</span>: absolute;\n  <span class=\"hljs-attribute\">top</span>: <span class=\"hljs-number\">0</span>;\n  <span class=\"hljs-attribute\">left</span>: <span class=\"hljs-number\">0</span>;\n  <span class=\"hljs-attribute\">width</span>: <span class=\"hljs-number\">100%</span>;\n  <span class=\"hljs-attribute\">height</span>: <span class=\"hljs-number\">100%</span>;\n}\n</code></pre>\n<h2>References</h2>\n<ul>\n<li>Css Tricks: <a href=\"https://css-tricks.com/NetMag/FluidWidthVideo/Article-FluidWidthVideo.php\">Fluid Width Video</a></li>\n<li>Bootstrap: <a href=\"https://getbootstrap.com/components/#responsive-embed\">Responsive Embed</a></li>\n</ul>\n"}

