	module.exports = {"title":"Styling in Gatsby","date":"2017-02-16T00:00:00.000Z","path":"/2017-02-16-styling-in-gatsby/","body":"<p>應該有些人有發現</p>\n<p>之前一進 Blog 的時候樣式會先跑掉過一下才渲染出來</p>\n<p>前幾天把這個問題修掉了</p>\n<!--more-->\n<p>這算是一個 Server Side Render (SSR) 跟 Styling 問題</p>\n<h2>Server Side Render</h2>\n<p>普通的 React App 通常只有一個很小的 html</p>\n<p>所有的東西都是前端跑 javascript 用 React 生出來的</p>\n<p>所以在跑 javascript 之前頁面會是空白的</p>\n<p>Server Side Render 就是先在 server 上把 React 跑出來的東西存成 html 送出去</p>\n<p>這樣在跑 javascript 之前就已經有東西顯示在頁面上了</p>\n<p>要做 SSR 是因為如果頁面都是用 javascript 產生的</p>\n<p>搜尋引擎可能會看不懂，就沒辦法做 SEO</p>\n<h2>Styling</h2>\n<p>React 做 styling 的方式有好幾種</p>\n<ul>\n<li>inline style:</li>\n</ul>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">const</span> Content = <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> &lt;div style={{ <span class=\"hljs-attr\">padding</span>: <span class=\"hljs-number\">10</span> }} /&gt;\n</code></pre>\n<ul>\n<li>直接用 css:</li>\n</ul>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-class\">.content</span> {\n  <span class=\"hljs-attribute\">padding</span>: <span class=\"hljs-number\">10px</span>;\n}\n</code></pre>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">const</span> Content = <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> &lt;div className=<span class=\"hljs-string\">\"content\"</span> /&gt;\n</code></pre>\n<ul>\n<li>用 css module:</li>\n</ul>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-class\">.content</span> {\n  <span class=\"hljs-attribute\">padding</span>: <span class=\"hljs-number\">10px</span>;\n}\n</code></pre>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">import</span> styles <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'./styles.css'</span>;\n\n<span class=\"hljs-keyword\">const</span> Content = <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> &lt;div className={styles.content} /&gt;\n</code></pre>\n<ul>\n<li>用 javascript 處理 style</li>\n</ul>\n<p>由於大家在寫 CSS 遇到許多問題，像是 Global namespace</p>\n<p>就有人提出了 CSS in JS 的 solution</p>\n<p>可以參考這個 <a href=\"https://speakerdeck.com/vjeux/react-css-in-js\">slides</a></p>\n<p>比較紅的 library 有 <a href=\"https://github.com/Khan/aphrodite\">aphrodite</a>, <a href=\"https://github.com/threepointone/glamor/\">Glamor</a>, <a href=\"https://github.com/FormidableLabs/radium\">radium</a>, <a href=\"https://github.com/styled-components/styled-components\">styled-components</a></p>\n<p>目前看起來 styled-compoents 是我比較喜歡的方法</p>\n<p>這是那個作者的 talk，滿不錯的整理了各種 React Styling 的方法</p>\n<p><div class=\"embed-responsive embed-responsive-16by9\"><iframe class=\"embed-responsive-item\" id=\"youtubeplayer\" type=\"text/html\" width=\"640\" height=\"390\" src=\"//www.youtube.com/embed/19gqsBc_Cx0\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe></div></p>\n<h2>SSR + Styling</h2>\n<p>在做 SSR 的時候，有些人會把 style 從 javascript 裡拉出來</p>\n<p>因為就像前面提到的，這樣在 javascript 執行之前就可以看到樣式了</p>\n<p>像是 webpack 有一個插件 <a href=\"https://github.com/webpack-contrib/extract-text-webpack-plugin\">extract-text-webpack-plugin</a></p>\n<p>他會把所有的 `import styles from ‘./styles.css’ 集合起來存成一個檔案</p>\n<p>另外各種 CSS in JS 的方法也都有各自處理的方式</p>\n<p>像 aphrodite 跟 glamor 的 api 就滿像的 <s>跟本一樣吧</s></p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">import</span> { StyleSheetServer } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'aphrodite'</span>;\n\n<span class=\"hljs-keyword\">const</span> { html, css } = StyleSheetServer.renderStatic(<span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span>\n  ReactDOMServer.renderToString(<span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">App</span>/&gt;</span></span>));\n</code></pre>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">import</span> { renderStatic } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'glamor/server'</span>\n\n<span class=\"hljs-keyword\">const</span> { html, css, ids } = renderStatic(<span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span>\n  ReactDOMServer.renderToString(<span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">App</span>/&gt;</span></span>));\n</code></pre>\n<p>styled-components 的 SSR 目前還沒有 public api</p>\n<p>跟據這個 <a href=\"https://github.com/styled-components/styled-components/issues/124\">issue</a> 會在 v2 的時候出來</p>\n<p><s>作者去年 12月說一月初會發佈，一月說二月初會發佈</s></p>\n<p>目前只能用 issue 裡提到的 workaround</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">import</span> styleSheet <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'styled-components/lib/models/StyleSheet'</span>\n\n<span class=\"hljs-comment\">/* before each render */</span>\nstyleSheet.flush()\n\n<span class=\"hljs-comment\">/* after each render */</span>\nstyleSheet.rules().map(<span class=\"hljs-function\"><span class=\"hljs-params\">rule</span> =&gt;</span> rule.cssText).join(<span class=\"hljs-string\">'\\n'</span>)\n</code></pre>\n<h2>Styling in Gatsby</h2>\n<p>講完這些就可以輕鬆解決我之前頁面剛載入時樣式會跑掉的問題了</p>\n<p>因為 Gatsby 有用 extract-text-webpack-plugin</p>\n<p>而我用了 styled-components 一開始沒對 SSR 做處理</p>\n<p>所以頁面載入時就會先渲染他有 extract 到的一些基本樣式</p>\n<p>然後在 javascript 跑起來之後 React 才會把剩下的樣式加進去</p>\n<p>解決方式就是上一個段落講的那樣</p>\n"}

