	module.exports = {"title":"AMP project","date":"2017-02-20T00:00:00.000Z","path":"/2017-02-20-amp-project/","featuredImage":"https://i.imgur.com/lqxEEtF.png","body":"<p>最近用 Google Search 的時候很常看到搜尋結果有一個 <span style=\"display: inline-block; width: 20px; height: 20px; line-height: 20px; text-align: center; border-radius: 50%; color: white; background-color: black\">⚡</span> AMP 的標記</p>\n<p>前一陣子好奇查了一下發現是拿來加速手機瀏覽網頁的東西</p>\n<!--more-->\n<p>然後又剛好看到 geniustanley 大大的這篇 <a href=\"https://geniustanley.github.io/2017/02/20/Google-AMP-1/\">blog</a></p>\n<p>就想說來動手玩看看</p>\n<h2>What is AMP?</h2>\n<p>AMP 簡單分成三個部份</p>\n<h3>AMP HTML</h3>\n<p>用他<a href=\"https://www.ampproject.org/docs/reference/spec\">規定</a>好的方式來寫 HTML</p>\n<p>像是使用 AMP 自訂的 tag ，如 <code>&lt;amp-img&gt;</code></p>\n<p>然後跟其他方法結合來達到 performance 的提昇</p>\n<h3>AMP JS</h3>\n<p><a href=\"https://github.com/ampproject/amphtml/tree/master/src\">AMP JS library</a> 裡面實做了各種 <a href=\"https://www.ampproject.org/learn/how-amp-works/\">AMP 加速的方法</a></p>\n<p>像是 lazy-loaded resources</p>\n<p>如果用了 <code>&lt;amp-img&gt;</code> 他只會先抓使用者可見範圍裡面的圖</p>\n<p>這樣可以用最快的時間產生最少需要的畫面</p>\n<p>讓使用者感受到更少的畫面延遲</p>\n<h3>Google AMP Cache</h3>\n<p>看到 Cache 這個字就是跟快有關係</p>\n<p><a href=\"https://developers.google.com/amp/cache/\">Google AMP Cache</a> 會把合格的 AMP 網頁用 cdn 的方式存起來</p>\n<p>總之就是更快</p>\n<h3>AMP-ify</h3>\n<p>看完 AMP 的介紹之後就是要動手把自己的 Blog AMP-ify 一下</p>\n<p>Google 了一下發現 Gatsby 社群裡有人已經寫了一個小小的 <a href=\"https://github.com/chiedo/gatsby-amp-starter-blog/blob/master/ampify.js\">script</a> 把網頁轉 AMP</p>\n<p>我就拿來做了一點小修改 (<code>amp-img</code> 的 layout 跟 <code>&lt;a href&gt;</code> 的連結)</p>\n<p>然後套用在自己的 Blog</p>\n<p>大概經過半小時的努力</p>\n<p>終於讓我的 Blog 在 Google Search 的時候也會有潮潮的閃電啦</p>\n<p><img src=\"https://i.imgur.com/f4sPDUn.png\" alt=\"AMP\"></p>\n<h2>References</h2>\n<ul>\n<li>AMP: <a href=\"https://www.ampproject.org/learn/about-amp/\">What is AMP</a></li>\n<li>Geniustanley: <a href=\"https://geniustanley.github.io/2017/02/20/Google-AMP-1/\">Google AMP</a></li>\n<li>Gastby AMP Starter: <a href=\"https://github.com/chiedo/gatsby-amp-starter-blog/blob/master/ampify.js\">ampify.js</a></li>\n</ul>\n"}

