	module.exports = {"title":"字體調教心得","date":"2017-03-17T00:00:00.000Z","path":"/2017-03-17-playing-with-fonts-configuration/","featuredImage":"https://i.imgur.com/EHWNWpl.png","body":"<p>一陣子沒發文了 QQ</p>\n<p>來分享一下最近在 Archlinux 上調教字體的小小心得</p>\n<p>主要是設定 powerline 以及 emoji</p>\n<!--more-->\n<h2>Powerline</h2>\n<p><a href=\"https://github.com/powerline/powerline\">Powerline</a> 是一個很炫的 theme</p>\n<p>常用在各種 shell 的 prompt 還有 vim, tmux 的 statusline</p>\n<p><img src=\"https://i.imgur.com/NwdBdcK.png\" alt=\"powerline\"></p>\n<p>其中那些三角型都是 unicode 字元    </p>\n<p>很容易出現字體不支援的狀況</p>\n<p><img src=\"https://i.imgur.com/lfWR5f4.png\" alt=\"no-powerline\"></p>\n<p>很多人的做法都是去做字體檔的<a href=\"https://github.com/powerline/fonts\">補丁</a></p>\n<p>如果用的是不常見的字體沒人幫做補丁就會很困擾</p>\n<p>但我發現那個 repo 裡有一個只包含那些字元的字體 <a href=\"https://github.com/powerline/powerline/tree/master/font\">PowerlineSymbols</a></p>\n<p>如果系統有自己處理字型 fallback 的話應該就能正常顯示了</p>\n<p>這樣就可以自由使用任何字體 + Powerline 了</p>\n<p>如果沒有的話可以參考下面的 Fontconfig</p>\n<h2>Fontconfig</h2>\n<p><a href=\"https://www.freedesktop.org/wiki/Software/fontconfig/\">Fontconfig</a> 是一個設定字體的庫</p>\n<p>大部分現代 Linux 裡的應用都是用這個庫來選擇及渲染字體的</p>\n<p>他可以設定系統裡襯線、無襯線、等寬的預設字型</p>\n<p>還可以在系統匹配字體時插入別的字體 <em>(ex: emoji)</em></p>\n<h3>Font Path</h3>\n<p>fontconfig 預設會在這兩個目錄底下搜尋字體檔</p>\n<ul>\n<li>/usr/share/fonts/</li>\n<li>~/.local/share/fonts</li>\n</ul>\n<p>可以用 <code>fc-list</code> 這個指令來查看系統中安裝了哪些字體</p>\n<h3>Fontconfig configuration</h3>\n<p>fontconfig 主要從以下幾個路徑搜尋設定檔</p>\n<ul>\n<li>/etc/fonts/fonts.conf</li>\n<li>/etc/fonts/conf.d/</li>\n<li>~/.fonts.conf</li>\n<li>~/.fonts.conf.d/</li>\n<li>$XDG_CONFIG_HOME/fontconfig/fonts.conf</li>\n<li>$XDG_CONFIG_HOME/fontconfig/conf.d/</li>\n</ul>\n<p><em><code>$XDG_CONFIG_HOME</code> 是存放使用者各種設定的目錄，預設為 <code>~/.config</code></em></p>\n<p><em>Arch Wiki 說 fontconfig 2.10.1 以後 <code>~/.fonts.conf</code> 跟 <code>~/.fonts.conf</code> deprecated 了</em></p>\n<p>Fontconfig 的設定檔使用 xml 的格式</p>\n<pre><code class=\"language-xml\"><span class=\"hljs-comment\">&lt;!-- fontconfig template --&gt;</span>\n<span class=\"php\"><span class=\"hljs-meta\">&lt;?</span>xml version=<span class=\"hljs-string\">\"1.0\"</span><span class=\"hljs-meta\">?&gt;</span></span>\n<span class=\"hljs-meta\">&lt;!DOCTYPE fontconfig SYSTEM \"fonts.dtd\"&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">fontconfig</span>&gt;</span>\n\n  <span class=\"hljs-comment\">&lt;!-- settings go here --&gt;</span>\n\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">fontconfig</span>&gt;</span>\n</code></pre>\n<p>詳細的文件可以用 <code>man fonts-conf</code> 來查看</p>\n<p>這邊分享一下<a href=\"https://github.com/geniusgordon/dotfiles/blob/master/fonts.conf\">我自己的設定</a></p>\n<p>主要是拿來設定 emoji 的</p>\n<pre><code class=\"language-xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">match</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">test</span> <span class=\"hljs-attr\">name</span>=<span class=\"hljs-string\">\"family\"</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>sans-serif<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">test</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">edit</span> <span class=\"hljs-attr\">name</span>=<span class=\"hljs-string\">\"family\"</span> <span class=\"hljs-attr\">mode</span>=<span class=\"hljs-string\">\"prepend\"</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>Noto Color Emoji<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">edit</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">match</span>&gt;</span>\n</code></pre>\n<p>這段設定的意思是</p>\n<p>當我要使用無襯線字體 (sans-serif) 的時候</p>\n<p>在前面插入 (prepend) Noto Color Emoji</p>\n<p>有點像 css 在設定 font-family</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">font-family</span>: \"<span class=\"hljs-selector-tag\">Noto</span> <span class=\"hljs-selector-tag\">Color</span> <span class=\"hljs-selector-tag\">Emoji</span>\", <span class=\"hljs-selector-tag\">sans-serif</span>;\n              ^^^^^^^^^^^^^^^^\n      在 <span class=\"hljs-selector-tag\">sans-serif</span> 前插入 <span class=\"hljs-selector-tag\">Noto</span> <span class=\"hljs-selector-tag\">Color</span> <span class=\"hljs-selector-tag\">Emoji</span>\n</code></pre>\n<p>這樣一來不管在什麼地方使用到 emoji</p>\n<p>系統都會優先使用 Noto Color Emoji 來渲染</p>\n<h2>Color Emoji in Terminal</h2>\n<p>Linux 裡面常見的 terminal 預設都不支援有顏色的 emoji</p>\n<p><img src=\"https://i.imgur.com/SZIGEOQ.png\" alt=\"no-color\"></p>\n<p>後來我在 SO 看到<a href=\"https://askubuntu.com/questions/781851/use-emoji-inside-terminal\">這篇</a>的這句話</p>\n<blockquote>\n<p>Color Emojis are not currently supported. Apparently there is a patched WIP version of <strong>libcairo</strong> that allows rendering colored emojis but it isn’t stable yet.</p>\n</blockquote>\n<p>找了一下發現 Archlinux 有人做了<a href=\"https://aur.archlinux.org/packages/cairo-infinality-ultimate-with-colored-emoji/\">這個包</a></p>\n<p>裝了之後他會把原本的 cairo 蓋過去</p>\n<p>就可以有漂亮的彩色 emoji 了！</p>\n<p><img src=\"https://i.imgur.com/umWKd81.png\" alt=\"color-emoji\"></p>\n<h2>References</h2>\n<ul>\n<li>Github: <a href=\"https://github.com/powerline/powerline\">Powerline</a></li>\n<li>Arch Wiki: <a href=\"https://wiki.archlinux.org/index.php/font_configuration\">Font Configuration</a></li>\n<li>StackOverflow: <a href=\"https://askubuntu.com/questions/781851/use-emoji-inside-terminal\">Use Emoji Inside Terminal</a></li>\n</ul>\n"}

