	module.exports = {"title":"Next Level of Window Management","date":"2017-03-30T00:00:00.000Z","path":"/2017-03-30-next-level-of-window-management/","featuredImage":"https://i.imgur.com/DtjbjrI.png","body":"<p>平常沒事會逛 Reddit 的 <a href=\"https://www.reddit.com/r/unixporn/\">unixporn</a>，看看大家都怎麼客製化自己的 linux dekstop</p>\n<p>發現很多人都會用 <a href=\"https://i3wm.org/\">i3</a> - 一個視窗管理神器</p>\n<p>最近想說來用看看，不用還好，用了直接進入了 Linux Geek 的新境界</p>\n<!--more-->\n<h2>Window Manager</h2>\n<p>i3 是一種 window manager，先來介紹一下 window manager 是什麼</p>\n<p>Window manager 簡稱 wm，就跟字面上的意思一樣是負責管理視窗的東西，主要可以分成兩種</p>\n<p><strong>Compositing Window Manager</strong></p>\n<p><img src=\"https://i.imgur.com/eKEZkV8.png\" alt=\"Messy Desktop\"></p>\n<p>常見的視窗管理都是這類的，桌面像桌子，視窗像紙一樣重疊擺在桌上</p>\n<p><strong>Tiling Window Manager</strong></p>\n<p><img src=\"https://i.imgur.com/f3ccPE5.png\" alt=\"Oranized drawer\"></p>\n<p>Tiling window manager 不重疊視窗，把畫面分成一格一格的，比較像桌子的抽屜</p>\n<h2>Why Tiling Window Manager?</h2>\n<p>寫程式的人常常需要一邊開編輯器一邊開瀏覽器查東西，可能還要開很多有的沒有的東西</p>\n<p>像我在寫 react-native 的時候就需要開</p>\n<ul>\n<li>Android emulator</li>\n<li>Chrome</li>\n<li>一個 Terminal 開 vim、一個 dev server、一個看 log</li>\n<li>有時候可能還要開一個 Redux Debugger</li>\n</ul>\n<p>如果在加上一些通訊軟體 (Messenger、Line、Telegram、Slack)</p>\n<p>視窗一多，桌面的視窗很容易就變的亂亂的</p>\n<p>雖然 terminal 的部分可以用 tmux 處理</p>\n<p>gnome 也有 workspace 可以開多個桌面</p>\n<p>但是傳統的 window manager 常常會需要自己移動跟縮放視窗避免他們擋到別人</p>\n<p>有時候還是會覺得用起來卡卡的</p>\n<p>Tiling window manager 就可以很輕鬆的排版視窗，然後用各種快截鍵做高效率的操作</p>\n<p>用起來大概就像最上面那張圖一樣</p>\n<h2>i3</h2>\n<p>我第一個接觸的 tiling wm，其他的還沒試過，無法做比較</p>\n<p>分享一下自己的<a href=\"https://github.com/geniusgordon/dotfiles\">設定</a>跟客製化的東西</p>\n<p>很多都是參考 unixporn 上面分享的，目前還在持續改進中</p>\n<h2>Status bar</h2>\n<p>i3 的 status bar 用了他自己的 <a href=\"https://i3wm.org/docs/i3bar-protocol.html\">i3bar protocol</a></p>\n<p>做法是在 i3 config 裡設定</p>\n<pre><code><span class=\"hljs-class\">bar </span>{\n  status_command <span class=\"hljs-params\">&lt;command&gt;</span>\n}\n</code></pre>\n<p>那個 command 要 output 一個 infinite json array, 最簡單的例子如下</p>\n<pre><code class=\"language-json\">[\n  [{<span class=\"hljs-attr\">\"full_text\"</span>: <span class=\"hljs-string\">\" 17: 10\"</span>}, {<span class=\"hljs-attr\">\"full_text\"</span>: <span class=\"hljs-string\">\"  89%\"</span>}],\n  [{<span class=\"hljs-attr\">\"full_text\"</span>: <span class=\"hljs-string\">\" 17: 11\"</span>}, {<span class=\"hljs-attr\">\"full_text\"</span>: <span class=\"hljs-string\">\"  88%\"</span>}],\n  [{<span class=\"hljs-attr\">\"full_text\"</span>: <span class=\"hljs-string\">\" 17: 12\"</span>}, {<span class=\"hljs-attr\">\"full_text\"</span>: <span class=\"hljs-string\">\"  87%\"</span>}],\n  ...\n</code></pre>\n<p>他就會把他顯示在 status bar 上</p>\n<p><img src=\"https://i.imgur.com/pfBri9v.png\" alt=\"i3bar example\"></p>\n<p>所以可以自己寫一個 script，寫任何你想要顯示在 status bar 上面的東西</p>\n<p>我用了 <a href=\"https://github.com/vivien/i3blocks\">i3blocks</a> 來輔助設定，他可以搭配一個 config file，比較好管理自己寫的 script</p>\n<p>我的目前長這樣，還滿普通的</p>\n<p><img src=\"https://i.imgur.com/bMwz9Fi.png\" alt=\"i3blocks\"></p>\n<h2>Notification</h2>\n<p>用了 i3 就不會有像 Gnome, Ubuntu Unity 那種預設的通知泡泡</p>\n<p>雖然沒有別人幫你做好的東西，要自己設定，但這是缺點也是優點，就是可以自由的客製化</p>\n<p>我用的是 <a href=\"https://github.com/dunst-project/dunst\">dunst</a>，他滿容易設定的，目前長這樣，也是滿普通的</p>\n<p><img src=\"https://i.imgur.com/l1gQKyp.png\" alt=\"dunst\"></p>\n<p>還有一些 icon 問題沒處理，像上面這個 telegram 就只有顯示系統預設的 icon</p>\n<h2>Lock Screen</h2>\n<p>這個我還沒設定完，目前在用 i3 官方的 <a href=\"https://github.com/i3/i3lock\">i3lock</a></p>\n<p>我還有看到一些別人擴充 i3lock 做的東西，像是 <a href=\"https://github.com/meskarune/i3lock-fancy\">i3lock-fancy</a> 把桌面變模糊來當背景</p>\n<p><img src=\"https://raw.githubusercontent.com/meskarune/i3lock-fancy/master/screenshot.png\" alt=\"i3lock-fancy\"></p>\n<p>不過他的 script 跑的有點慢，我就沒用了</p>\n<h2>結語</h2>\n<p>我覺得 i3 跟 arch linux 還有 vim 有點像，都是不好上手，但是用了之後會很爽</p>\n<p>入坑 i3，又 Geek 了一個階層呢</p>\n"}

